.first {
  position: relative;
  padding-top: 36.8vh;
  height: 100vh;

  &__container {
    z-index: 100;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    height: 100%;
  }

  &__info {
    z-index: 100;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    max-width: 920px;
  }

  &__title {
    z-index: 100;
    margin-bottom: 30px;
    font-family: "Space Mono";
    font-weight: 700;
    font-style: italic;
    font-size: 80px;
    line-height: 80px;
    text-align: center;
    text-transform: uppercase;
    color: var(--color-purple);
  }

  &__descr {
    z-index: 100;
    max-width: 450px;
    line-height: 30px;
    text-align: center;
    color: var(--color-white);
  }
}

.to-projects {
  z-index: 100;
  display: flex;
  flex-direction: column;
  align-items: center;
  align-self: flex-end;
  margin-bottom: 85px;
  width: 100%;

  &__text {
    margin-bottom: 10px;
    color: var(--color-white);
  }

  &__btn {
    @include btn-reset;

    width: 15px;
    height: 45px;
    background-image: url(../img/arrow.svg);
    background-repeat: no-repeat;
  }
}

.stars,
.twinkling {
  z-index: 40;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  display: block;
}

.stars {
  z-index: 0;
  background-attachment: fixed;
  background: #000 url(../img/stars.png) repeat top center;
}

.twinkling {
  background-attachment: fixed;
  background-image: url(../img/twinkling.png);
  animation: 3s twinkle infinite;
  animation-timing-function: linear;
}

@keyframes twinkle {
  from {
    backrgound-position: 0;
  }

  to {
    background-position: -100px 0;
  }
}
